
html, body, #app {
  height: 100%;
}
html {
  box-sizing: border-box;
  overflow-y: scroll;
}
body {
  margin: 0;
  background-color: #FFFFFF;
  font-size: 16px;
  color: #333333;
  font-family: "Avenir", "Helvetica Neue",Helvetica,Arial,sans-serif;
  line-height: 1.6;
}
#app {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  max-width: 414px;
  margin: 0 auto;
}
*,
::before,
::after {
  box-sizing: inherit;
}
input,
button,
select,
textarea {
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
}
a {
  text-decoration: none;
  color: #666;
}
input,
select,
div,
a,
button {
  outline: none;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  -webkit-tap-highlight-color: transparent;
  margin: 0;
  padding: 0;
  background: none;
  border: none;
}
nav {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  height: 50px;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  border-bottom: 1px solid #EFEFEF;
}
nav a {
    height: 100%;
    line-height: 50px;
    padding: 0 5px;
}
nav a.active {
      color: orange;
      border-bottom: 2px solid orange;
}
.slide-fade-enter {
  opacity: 0;
  -webkit-transform: translateX(-30px);
          transform: translateX(-30px);
}
.slide-fade-enter-active, .slide-fade-leave-active {
  transition: all .5s ease;
}
.slide-fade-leave {
  opacity: 0;
}
.content {
  position: relative;
}
.content .page-wrap {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
}



